@import url('https://cdn.syncfusion.com/ej2/material.css');

.sidebar {
  box-shadow: rgb(113 122 131 / 11%) 0px 7px 30px 0px;
}

.nav-item,
.navbar {
  z-index: 10000;
}

@media screen and (max-width:800px) {
  .sidebar {
    z-index: 10000000;
  }
}

.e-dlg-center-center,
.e-quick-popup-wrapper.e-device {
  z-index: 1000000 !important;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(216, 216, 216);
  border-radius: 40px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

/* color-picker style  */

.lightPreview {
  background: transparent url('https://res.cloudinary.com/hellobryan/image/upload/v1668570502/dashboard/PreviewLight_rugzuq.png') no-repeat;
  display: inline-block;
  height: 80px;
  margin: 10px 0;
  min-width: 600px;
  max-width: 600px;
}

.darkPreview {
  background: transparent url('https://res.cloudinary.com/hellobryan/image/upload/v1668570832/dashboard/PenDark.png_uy4xik.png') no-repeat;
  display: inline-block;
  height: 80px;
  margin: 10px 0;
  min-width: 600px;
  max-width: 600px;
}

.e-input-group:not(.e-float-icon-left),
.e-input-group.e-success:not(.e-float-icon-left),
.e-input-group.e-warning:not(.e-float-icon-left),
.e-input-group.e-error:not(.e-float-icon-left),
.e-input-group.e-control-wrapper:not(.e-float-icon-left),
.e-input-group.e-control-wrapper.e-success:not(.e-float-icon-left),
.e-input-group.e-control-wrapper.e-warning:not(.e-float-icon-left),
.e-input-group.e-control-wrapper.e-error:not(.e-float-icon-left) {
  border: none;
}